.review{
  .match-type {
    width: 100%;
    height: .74rem;
    background: #fff;
    text-align: left;
    box-sizing: border-box;
    padding-left: .34rem;
    line-height: .74rem;
    display: flex;
    align-items: center;
    position: relative;

    li{
      display: inline-block;
      text-align: center;
      width: 1.06rem;
      height: .4rem;
      line-height: .38rem;
      box-sizing: border-box;
      border-radius: .2rem;
      border:1px solid #d0d0d0;
      font-size: .24rem;
      color:rgba(35,35,35,.3);
      margin-right:.2rem;
    }
    li.active{
      border:none;
      background: #4154c5;
      color:#fff;
      line-height: .4rem;
    }
  }

}
.title{
  text-align :left;
  padding :0 .38rem;
  font-size :.26rem;
  height: .76rem;
  line-height: .76rem;
  background: #f4f4f4;
  img{
    width: .4rem;
    vertical-align: middle;
    margin-right: .19rem;
  }
  span{
    vertical-align: middle;
  }
}
.main{
  background :#ffffff;
  .bold{
    position: relative;
  }
  .bold:before{
    content:'';display: block;
    width: .08rem;
    height: 100%;
    background: #ff4333;
    position: absolute;
    top:0;
    left: 0;
  }
  .bold:after{
    content:'';display: block;
    width: .08rem;
    height: 2px;
    background: #ff4333;
    position: absolute;
    bttom:-1px;
    left: 0;
  }
  .item-container{
    width: 100%;
    background: #fff;
    position: relative;

    .item-match-box{
      width: 100%;
      height: 100%;
      position: relative;
      border-bottom :1px solid #eee;
    }
    .item-match-box&:last-child{
      border-bottom: none;
    }
    .firsr-a{
      display: block;
      position: absolute;
      width: 100%;
      height: 1.4rem;
      background: rgba(0,0,0,0);
      top:0;
      left: 0;
      z-index: 100;
    }
    .list-item{
      display: block;
      width: 100%;
      height :1.74rem;
      box-sizing: border-box;
      .game{
        height: 100%;
        .game-name{
          width: 1.64rem;
          height: 100%;
          padding-left: .35rem;
          text-align: left;
          display: flex;
          align-content: center;
          flex-wrap: wrap;
          .iconImg{
            width: 100%;
            font-size: .26rem;
            color:#9d9d9d;
          }
          .name{
            padding-top: .24rem;
            text-align: left;
            box-sizing: border-box;
            color:#9d9d9d;
            font-size: .20rem;
            line-height: .3rem;
            width: 100%;
          }
        }
        .game-info{
          height: 100%;
          width: 3.58rem;
          box-sizing: border-box;
          padding-left: .42rem;
          .special-game{
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding-right: .2rem;
            position: relative;
            height: 100%;
            font-size:.26rem;
            line-height: .3rem;
            text-align: left;
            .show-content{
              width: 100%;
              height: 100%;
              position: relative;
              top:50%;
              transform: translateY(-50%);
            }
            img{
              width: .48rem;
              height: .48rem;
              margin-right: .1rem;
              vertical-align: middle;
              display: inline-block;
              position: relative;
              top:50%;
              transform: translateY(-50%);
            }
            .word{
              vertical-align: middle;
              width: 75%;
              line-height: .35rem;
              position: absolute;
              left: .58rem;
              top:50%;
              transform: translateY(-50%);
              overflow : hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;

            }

          }
          .team{
            width: 100%;
            display: flex;
            align-content: center;
            height: 100%;
            flex-wrap: wrap;
            text-align: left;
            .teamInfo{
              width: 100%;
              img{
                width: .48rem;
                height: .48rem;
                margin-right: .1rem;
                vertical-align: middle;
              }
              span{
                display: inline-block;
                width: 75%;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: .28rem;
                vertical-align: middle;
              }
              &:nth-of-type(1){
                margin-bottom: .16rem;
              }
            }
          }
        }
        .playeIcon{
          position: absolute;
          display: block;
          border-radius: .21rem;
          text-align: center;
          font-size: .22rem;
          top:50%;
          transform: translateY(-50%);
          right:.50rem;
          padding:.1rem 0;
          color:#4154c5;
          img{
            width: .28rem;
          }
        }
      }
    }

  }

  .loadmore{
    width :100%;
    background :#f5f5f5;
    text-align :center;
    height :.8rem;
    line-height :.8rem;
    cursor :pointer;
    font-size :.26rem;
    a{
      display: block;
      width: 100%;
      height: 100%;
      color:#9b9b9b;
    }
    img{
      height :60%;
      margin-right: .2rem;
      vertical-align :middle;
    }
  }
  .nodata{
    width: 100%;
    background :#f5f5f5;
    padding:1rem 0 3.5rem;
    text-align: center;
    img{
      display: block;
      width: 60%;
      margin: 0 auto;
    }
  }
}



