
.appPopup{
  position: fixed;
  top:65%;
  right:.3rem;
  right:0;
  z-index: 9999;
  cursor: pointer;
}
.appPopup img{
  width: 1.3rem;
}
.appPopup a{
  display: block;
  width: 30%;
  height: 20%;
  /*background: rgba(0,0,0,.5);*/
  position: absolute;
  right:0;
  top:0;
}



